.gpt3_possibility{
  display: flex;
  flex-direction: row;
}

.gpt3_possibility-image{
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  margin-right: 2rem;
}

.gpt3_possibility-image img{
  width: 100%;
  height: 100%;
}

.gpt3_possibility-content{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

.gpt3_possibility-content h4{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;

  color: bisque;
  margin-bottom: 2rem;
}

.gpt3_possibility-content h4:last-child{
  color: brown;
  margin-top: 2rem;
}

.gpt3_possibility-content h1{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin-bottom: 2rem;
}

.gpt3_possibility-content p{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;

  color: var(--color-text);

  margin-bottom: 2rem;
}

@media screen and (max-width:950px) {
  .gpt3_possibility{
    flex-direction: column;
  }

  .gpt3_possibility-image{
    margin: 1rem 0;
  }

  .gpt3_possibility-content{
    margin-top: 2rem;
  }
}