.gpt3_features-container_feature{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;

  margin: 1rem;
}

.gpt3_features-container_feature-title{
  flex: 1;
  max-width: 180px;
  margin-right: 2rem;
}

.gpt3_features-container_feature-title h1{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 24px;
  
  color: aliceblue;
}
.gpt3_features-container_feature-title div{
  width: 38px;
  height: 3px;

  background: var(--gradient-bar);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.25rem;
}
.gpt3_features-container_feature-text{
  flex: 2;
  max-width: 600px;
  color: aliceblue;
  display: flex;
  font-family: var(--font-family);
}

.gpt3_features-container_feature-text p{
  font-family: var(--font-family);
  font-weight: 500;
  font-style: normal;
  font-size: 16px;
  line-height: 30px;
}

@media screen and (max-width:550px) {
  .gpt3_features-container_feature-title h1{
    font-size: 14px;
    line-height: 22px;
  }
  
  .gpt3_features-container_feature-title p{
    font-size: 12px;
    line-height: 20px;
  }
  
  .gpt3_features-container_feature{
    margin: 1rem 0;
  }
}


